  /*  0 */
  bottom: 0;
  /*  0 */
  visibility: hidden;
  /*   */
  /* transition  */
  transition: 0.5s ease;
}

.overlay:target {
  visibility: visible;
  /*   */
  opacity: 0.8;
  /*     */
}

.popup {
  position: fixed;
  /*   */
  visibility: hidden;
  /*  -  */
  z-index: 9999;
  /* z-index */
  transform: translateX(-50%);
  left: 50%;
  top: 20px;
  width: 370px;
  padding: 25px 45px;
  background-color: #f4f4f4;
  /*  */
  border-radius: 7px;
}

.overlay:target + .popup {
  opacity: 1;
  /*   */
  visibility: visible;
  /*   */
}

.popup-close {
  display: none;
  width: 3px;
  height: 20px;
  background: red;
  position: absolute;
  top: 7px;
  right: 15px;
  font-size: 0px;
  transform: rotate(45deg);
}

.popup-close:after {
  content: "";
  display: block;
  transform: rotate(-90deg);
  width: 3px;
  height: 20px;
  background: red;
  position: absolute;
  top: 0;
}

